import React from "react";
import "./style.css";
export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.handleMessageChange = this.handleMessageChange.bind(this);
    this.handleNameChange = this.handleNameChange.bind(this);
    this.state = {
      message: "",
      senderName: ""
    };
  }

  handleMessageChange(ta) {
    this.setState({ message: ta.target.value });
  }

  handleNameChange(ta) {
    this.setState({ senderName: ta.target.value });
  }

  render() {
    return (
      <div className="container">
        <center>
          <h1>
            <span className="special-letter">Venkatesh Kocherlakota</span>
          </h1>
        </center>
        <p className="card">
          <br />
          Hello There ! Welcome to my portfolio ! <br />
          <br />
          I am a very curious person with a keen interest in technology and a
          burning desire to learn new things. Thats what motivates me out of my
          bed everyday in the morning!
          <br />
          <br />
          Okay, now more about me. I love instrumental music and connecting with
          nature. Nature has a soothing way to cleanse our busy lives and music
          on top of that makes you feel great! So, connect with nature in your
          free time and learn a musical instrument if possible!
          <br />
          <br />
          Coming to technical aspects, I love machines. I like to code and
          design new stuff in my free time. As my background is from
          Electronics, I have interest and skill in both Eletronics and Computer
          Science. A unique and fun career always awaits you if you have broad
          interests!
          <br />
          <br />
          Feel free to contact me by dropping a mail:
          <br />
          <input
            type="text"
            value={this.state.senderName}
            onChange={this.handleNameChange}
            placeholder="your name here"
          />
          <br />
          <textarea
            id="textarea"
            rows="5"
            value={this.state.message}
            onChange={this.handleMessageChange}
            placeholder="your mesage here"
          />
          <br />
          <a
            href={
              "mailto:kocherlakotavenkatesh666@gmail.com?Subject=sender: " +
              this.state.senderName +
              "&body=" +
              this.state.message
            }
            target="_top"
          >
            <button>
              <b>Send</b>
            </button>
          </a>
        </p>
      </div>
    );
  }
}
