import React from "react";
export default class Skills extends React.Component {
  render() {
    return (
      <div className="container">
        <center>
          <h1>
            <span className="special-letter">My Skills</span>
          </h1>
        </center>
        <p className="card">
          I am <span className="special-letter">skilled</span> in the following
          disciplines:
        </p>
        <div className="left-box">
          <p className="card">
            <b>Computer Science:</b>
            <hr />
            <ul>
              <li>C#.Net</li>
              <li>Java</li>
              <li>HTML</li>
              <li>CSS</li>
              <li>JS</li>
              <li>ADO.Net</li>
              <li>XAML</li>
              <li>Windows Communication Foundation</li>
              <li>Windows Presentation Foundation</li>
              <li>Windows Installer XML Toolset 3</li>
              <li>VBScripting</li>
              <li>Batch Scripting</li>
              <li>Assembly Language</li>
            </ul>
          </p>
        </div>
        <div className="right-box">
          <p className="card">
            <b>Electronics:</b>
            <hr />
            <ul>
              <li>Arduino</li>
              <li>Digital Electronics</li>
              <li>Analog Electronics</li>
              <li>VHDL</li>
              <li>Microprocessors and Microcontrollers</li>
              <li>Signals and Systems</li>
            </ul>
          </p>
        </div>
      </div>
    );
  }
}
